// UI_AD_AA_01.html
.init {
  position: relative;
  height: 870px;
  text-align: center;

  .title {
    padding-top: 190px;
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 36px;
    line-height: 71px;

    img {
      position: absolute;
      left: 245px;
      top: 179px
    }

    &.pl {
      padding-left: 92px;
    }

    &.mb {
      margin-bottom: 80px;
    }
  }

  .sub {
    p {
      font-size: 36px;
      margin-bottom: 147px;
    }
  }

  .form_info {
    .form-control {
      margin: 0 auto;
    }

    .desc {
      display: block;
      font-size: 14px;
      line-height: 20px;
    }

    .domain {
      font-size: 14px;
      font-weight: 600;
      letter-spacing: .3px;
    }

    &.mb {
      margin-bottom: 160px;
    }

  }

  &.add_pf {
    height: 719px;
  }

  &.info-pf {
    height: auto;

    .title {
      img {
        left: 145px;
      }
    }

    .form-group {
      position: relative;

      .btn {
        position: absolute;
        width: 140px;
        height: 40px;
        right: 206px;
      }
    }
  }

  .btn-area {
    .btn {
      &.mr {
        margin-right: 117px;
      }
    }
  }

  &.detail {
    height: auto;

    .title {
      margin-bottom: 39px;
      letter-spacing: -.5px;

      img {
        left: 245px;
      }
    }

    .site_name {
      font-size: 14px;
      font-weight: bold;
      text-align: left;
      line-height: 20px;
      text-transform: uppercase;
      padding: 0 350px 0 361px;
      margin-bottom: 20px;
    }

    .add_img {

      .plus {
        display: block;
        color: #aaaaaa;
        margin: 33px 0 11px;
      }

      .txt {
        display: block;
        font-size: 14px;
        color: #aaaaaa;
      }

      a {
        display: block;
        width: 200px;
        height: 112px;
        border: 1px solid $gray-300;
        margin: 0 auto 54px;

        img {
          display: none;
          width: 100%;
          height: 110px;
        }
      }
    }
  }

  &.category {
    .title {
      white-space: nowrap;

      img {
        left: 61px;
      }

      &.pl {
        padding-left: 93px;
      }
    }

    .sub {
      p {
        margin-bottom: 61px;
      }
    }

    .form_info {
      margin-bottom: 105px;

      .btn {
        width: 140px;
      }

      .form-group {
        &.add {
          padding-left: 151px;
          margin-right: 7px;
        }
      }
    }
  }

  &.select {
    height: auto;

    .title {
      margin-bottom: 84px;

      img {
        left: 221px;
      }
    }

    .card-box {
      padding: 0 162px;
      margin-bottom: 97px;
    }

    .card {
      margin-right: 36px;

      .btn-area {
        display: none;
      }

      &:hover {
        .img {
          position: relative;

          &::after {
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            background-color: rgba(0, 0, 0, 0.7);
            left: 0;
            top: 0;
          }
        }

        .btn-area {
          display: inline-block;
          position: absolute;
          top: 48px;
          left: 30px;
        }
      }
    }
  }

  &.complete {
    height: auto;

    .title {
      font-weight: normal;
    }
  }

  &.login {
    .title {
      padding-top: 100px;
      margin-bottom: 17px;
    }

    .sub {
      p {
        margin-bottom: 107px;
      }
    }

    .btn-area {
      .btn {
        width: 400px;
        margin: 0 auto 20px;
        padding: 0;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    &.join {
      .title {
        margin-bottom: 120px;
      }
    }
  }

  &.select_template {
    .title_area {
      .title {
        padding-top: 111px;
        margin-bottom: 17px;
      }

      .sub {
        p {
          margin-bottom: 105px;
        }
      }
    }

    .d-center {
      display: flex;
      justify-content: center;
      -webkit-align-items: center;
      align-items: center;
      flex-wrap: wrap;
      margin-bottom: 100px;

      .site {
        width: 232px;
        box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
        margin-right: 40px;
        margin-bottom: 0;

        .site-img {
          img {
            height: 131px;
          }
        }

        .site-body {
          padding: 0 10px;
          background: $white;

          .title {
            padding: 0;
            text-align: left;
            margin-bottom: 0;
            line-height: 50px;
            font-size: 20px;
          }
        }

        .btn-area {
          left: 29px;
        }

        &.active {
          .site-body {
            .title {
              line-height: 42px;
            }
          }
        }
      }
    }

    .btn-area {
      .btn {
        &.mr {
          margin-right: 37px;
        }
      }
    }
  }
}

.site {
  position: relative;
  display: block;
  width: 250px;
  border: 1px solid #e0e0e0;
  margin-right: 20px;
  margin-bottom: 20px;

  .site-img {
    display: block;

    img {
      width: 100%;
      height: 142px;
    }
  }

  .site-body {
    display: block;
    //border-top: 1px solid #e0e0e0;
    padding: 0 20px;

    .title {
      display: block;
      font-size: 18px;
      font-weight: bold;
      line-height: 50px;
      width: 210px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .address {
      display: block;
      font-size: 12px;
      line-height: 18px;
      font-weight: normal;
      width: 210px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      margin-top: -2px;
      margin-bottom: 17px;
    }
  }

  .btn-area {
    display: none;
    position: absolute;
    top: 54px;
    left: 40px;
  }

  &:focus, &:hover {
    color: inherit;
  }

  .plus {
    text-align: center;
    font-size: 20px;
    color: #AAAAAA;
    margin-top: 93px;
    margin-bottom: 8px;
  }

  .txt {
    text-align: center;
    color: #AAAAAA;
    font-size: 14px;
  }

  &.active {
    border: 4px solid #d63d45;
  }

  &:hover {
    .site-img {
      position: relative;

      &::after {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: rgba(0, 0, 0, .7);
        left: 0;
        top: 0;
      }
    }

    .btn-area {
      display: block;

      &.single {
        left: 84px;
      }
    }
  }

  &.add {
    height: 227px;
  }
}

.main-bg {
  position: relative;
  overflow: hidden;

  .bar {
    height: 22.8px;
    position: absolute;
    bottom: 1px;

    &.left-bar {
      width: calc((50% - 47px));
      min-width: calc((50% - 47px));
      background-color: #C2C2C2;
      left: 0;
      top: 74.6px;
    }

    &.right-bar {
      width: calc((50% - 476px));
      min-width: calc((50% - 476px));
      background-color: #E99A91;
      right: 0;
      top: 74.8px;
    }
  }

  img {
    margin-left: calc((50% - 48px));
  }
}

.inner {
  .section-container {
    float: left;
    width: calc(100% - 300px);
    padding: 25px 40px;

    .title_area {
      margin-bottom: 23px;

      .title {
        display: inline-block;
        line-height: 41px;
        height: 41px;

        i {
          font-size: 28px;
          vertical-align: middle;
          padding-bottom: 4px;
          margin-right: 10px;
        }
      }

      .btn-area {
        padding-top: 6px;
        display: inline-block;
        float: right;

        .btn {
          margin-right: 2px;

          &:last-child {
            margin-right: 0;
          }
        }
      }

      .modal {
        .modal-body {
          .title {
            display: block;
            font-size: 48px;
            line-height: 71px;
            width: 672px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            margin: 0 auto;
          }

          .desc {
            font-size: 36px;
            line-height: 54px;
            width: 823px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            margin: 0 auto;
          }
        }

        .modal-footer {
          padding: 0 408px 50px;

          .btn {
            width: 47%;
          }
        }
      }
    }

    .contents {
      border-top: 2px solid #909090;
      padding: 40px 0 0;

      .inner {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-align-items: center;
        align-items: center;
        flex-wrap: wrap;

      }
    }

    &.edit {
      .title_area {
        &.manage {
          position: relative;
          margin-bottom: 56px;

          .title {
            font-size: 36px;
            line-height: 47px;
          }

          .project {
            position: absolute;
            font-size: 24px;
            font-weight: bold;
            line-height: 36px;
            top: 56px;
            left: 2px;
          }

          .btn-area {
            padding-top: 45px;
          }
        }
      }

      .contents {
        .inner {
          .site {
            &.add {
              height: 195px;

              .plus {
                margin-top: 79px;
              }
            }
          }
        }
      }

      .snb {
        .box {
          &.add_logo {
            margin-bottom: 44px;
          }

          .custom-control {
            &.mr {
              margin-right: 59px;
            }
          }
        }
      }
    }

  }

  .snb {
    float: right;
    width: 300px;
    height: 1040px;
    background-color: #f5f5f5;
    padding: 0 20px;

    .select {
      .title {
        font-size: 18px;
        font-weight: bold;
        margin-top: 204px;
        margin-bottom: 13px;
      }

      .desc {
        line-height: 22px;
        letter-spacing: -0.28px;
      }
    }

    .contents {
      padding-top: 50px;

      .box {
        margin-bottom: 43px;
        position: relative;

        .form_site {
          .form-group {
            .form-control {
              margin-bottom: 6px;
            }

            .site_title {
              font-size: 14px;
              line-height: 18px;
              padding: 0 10px;
            }

            &.active {
              .form-control {
                &::placeholder {
                  color: #333;
                }
              }
            }
          }
        }

        .form_intro {
          .img {
            width: 260px;
            height: 147px;

            img {
              width: 100%;
              height: 100%;
            }
          }
        }

        .add_logo {
          display: block;
          width: 260px;
          height: 146px;
          border: 1px solid #C2C2C2;
          margin-bottom: 8px;
          background-color: $white;

          .plus {
            font-size: 20px;
            text-align: center;
            color: #AAAAAA;
            margin-top: 49px;
            margin-bottom: 7px;
          }

          .txt {
            font-size: 14px;
            text-align: center;
            color: #AAAAAA;
          }

          img {
            width: 100%;
            height: 100%;
          }
        }

        .desc {
          font-size: 14px;
          line-height: 18px;
          padding-left: 10px;
          letter-spacing: -0.1px;
        }

        .btn-area {
          &.change {
            text-align: center;

            .btn {
              width: 116px;
            }
          }
        }

        &::after {
          content: '';
          position: absolute;
          background: url("../img/common/dot.png") no-repeat;
          width: 100%;
          height: 2px;
          bottom: -23px;
          left: 0;
        }

        &.add_logo {
          margin-bottom: 44px;
        }
      }

      .btn-area {
        .btn {
          width: 120px;
          margin-right: 15px;

          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
  }

}

.mypage {
  padding-top: 35px;

  .title_area {
    padding-bottom: 33px;
    border-bottom: 2px solid #909090;
    margin-bottom: 20px;

    .title {
      text-align: center;
    }
  }

  .list {
    li {
      list-style: none;
      position: relative;
      padding: 41px 60px 39px;

      &::after {
        position: absolute;
        display: block;
        content: '';
        background: url("../../dist/img/common/line.png");
        width: 100%;
        height: 2px;
        bottom: 0;
        left: 0;
      }

      .title {
        font-size: 14px;
        line-height: 20px;
        font-weight: bold;
      }

      .info {
        position: absolute;
        left: 340px;
        top: 20px;
        display: inline-block;
        font-size: 14px;
        line-height: 20px;
      }

      .profile {
        width: 64px;
        height: 64px;
        border: 1px solid #c8c8c8;
        border-radius: 50px;
        vertical-align: middle;
        text-align: center;

        i {
          font-size: 29px;
          color: #999999;
          padding-top: 16px;
        }

        img {
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }

      .top {
        top: 41px;
      }

      .txt {
        line-height: 20px;
        margin-left: 25px;
      }

      &:last-child {
        margin-bottom: 95px;
      }
    }
  }

  .delete {
    float: right;
    font-size: 18px;
    margin-bottom: 127px;
  }

  .modal {
    .modal-body {
      .desc {
        font-size: 18px;
        text-align: center;
        line-height: 24px;
      }

      .custom-control {
        .custom-control-label {
          font-size: 14px;
        }
      }

      .btn-area {
        padding-left: 80px;

        .btn {
          &:first-child {
            margin-right: 35px;
          }
        }

        &.confirm {
          text-align: center;

          .btn {
            width: 165px;
          }
        }
      }
    }
  }
}

.p_section {
  height: 870px;
  display: flex;
  align-items: center;
  justify-content: center;

  .img {
    display: block;
    width: 100%;
    height: auto;

    img {
      width: 100%;
      height: auto;
    }
  }

  .title {
    display: block;
    text-align: center;
    margin-top: 10px;
  }

  .title_area {
    padding-bottom: 33px;
    border-bottom: 2px solid #909090;
    margin-bottom: 40px;

    .title {
      text-align: center;
    }
  }

  a.col {
    margin-bottom: 40px;

    &.active {
      .img {
        border: 4px solid #d63d45;
      }

      .title {
        color: #d63d45;
      }
    }
  }

  &.list {
    display: unset;
    align-items: unset;
    justify-content: unset;

    .go_back {
      position: absolute;
      top: 115px;

      .prev {
        font-family: 'NotoSansCJKkr-Medium';
        font-size: 18px;
        letter-spacing: -.72px;

        i {
          margin-right: 10px;
          font-size: 18px;
        }
      }
    }

    .title_area {
      .title {
        margin-top: 28px;
      }
    }
  }

  &.slider {
    height: auto;
    padding: 50px 0;
  }
}

