.m-wrap {
  .introduction {
    padding-top: 119px;

    .title {
      font-size: 48px;
      font-weight: bold;
      text-align: center;
    }

    .desc_01, .desc_02 {
      font-size: 20px;
      line-height: 32px;
      text-align: center;
      letter-spacing: -0.1px;
    }

    .desc_01 {
      margin-top: 37px;
      margin-bottom: 32px;
    }

    .desc_02 {
      margin-bottom: 98px;
    }

    .btn-area {
      padding-bottom: 32px;
    }
  }

  .pofol {
    background: #f9f9f9;
    padding-top: 131px;
    padding-bottom: 155px;

    .title {
      font-size: 48px;
      font-weight: bold;
      text-align: center;
    }

    .desc_01, .desc_02 {
      font-size: 20px;
      line-height: 32px;
      text-align: center;
      letter-spacing: -0.1px;
    }

    .desc_01 {
      margin-top: 37px;
      margin-bottom: 165px;
    }

    .row {
      justify-content: center;
      justify-items: center;

      .col {
        width: 100%;
        flex-basis: auto;
        flex-grow: initial;
      }
    }
    .site {
      box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
      border: 0;
    }
  }

  .main-bg {
    margin-bottom: 84px;

    img {
      width: 300px;
      margin-left: calc((50% - 134px));
    }

    .bar {
      height: 14px;

      &.left-bar {
        width: calc((50% - 133px));
        min-width: calc((50% - 133px));
        top: 41px;
      }

      &.right-bar {
        width: calc((50% - 549px));
        min-width: calc((50% - 166px));
        top: 41px;
      }
    }
  }

  .site {
    width: 100%;
    height: 280px;

    .site-img {
      height: 198px;

      img {
        width: 100%;
        height: 100%;
      }
    }

    .site-body {
      padding: 15px 20px;
      background: $white;

      .title {
        text-align: left;
        width: auto;
        font-size: 20px;
      }
    }

    &:hover {
      .btn-area {
        &.single {
          left: 50%;
          top: 37%;
          transform: translate(-50%, -50%);
        }
      }
    }
  }
}

@media (min-width: 576px) {
  header {
    .container {
      .navbar-brand {
        img {
          width: auto;
          height: auto;
        }
      }

      .login {
        .text {
          display: inline-block !important;
        }
      }
    }
  }
  .m-wrap {
    .pofol {
      .site {
        .site-body {
          .title {
            width: auto;
          }
        }
      }
      .row {
        .col {
          width: 50%;
        }
      }
    }
  }
}

@media (min-width: 1200px) {
  .m-wrap {
    .main-bg {
      img {
        width: auto;
        margin-left: calc((50% - 134px));
      }

      .bar {
        height: 28px;

        &.left-bar {
          width: calc((50% - 133px));
          min-width: calc((50% - 133px));
          top: 99px;
        }

        &.right-bar {
          width: calc((50% - 549px));
          min-width: calc((50% - 549px));
          top: 100px;
        }
      }
    }

    .pofol {
      .site {
        .site-body {
          .title {
            width: 314px;
          }
        }

        &:hover {
          .btn-area {
            &.single {
              left: 50%;
              top: 37%;
              transform: translate(-50%, -50%);
            }
          }
        }
      }
      .row {
        .col {
          width: 33.3333%;
        }
      }
    }
  }
}

@media (max-width: 1200px) {
  .main-bg {
    .bar {
      &.right-bar {
        background-color: #E99A91;
        left: calc((50% + 476px));
      }
    }
  }
  .main-bg {
    .bar {
      &.right-bar {
        left: auto;
      }
    }
  }
}
